        <div class="main-bn about-bn">
            <div class="zems-img"><img class="zems" src="@/assets/img/about-zems.png"></div>
            <div class="about-bg-icon">
                <img src="@/assets/img/about-bg-icon.png" alt="">
            </div>
            <div class="mb-inner inner">
                <h1>Суть моего<br>проекта –<br>дать вам<br>настоящие<br>скидки!</h1>
                <p class="author">Алексей Земсков</p>
            </div>
        </div>
        <div class="about-wrapper">
            <div class="about-how-it-works about-block">
            <div class="bottom-bg-shadow"></div>
            <div class="block-ttl">Как это работает?</div>
            <ul class="stages">
                <li>
                    <div class="txt">Я нахожу поставщиков</div>
                    <div class="gradient-marker">1</div>
                    <div class="bott-arrow">
                        <svg class="icon-about-arrow">
                            <use xlink:href="#about-arrow"></use>
                        </svg>
                    </div>
                </li>
                <li>
                    <div class="txt">Они дают мне скидку</div>
                    <div class="gradient-marker">2</div>
                    <div class="bott-arrow">
                        <svg class="icon-about-arrow">
                            <use xlink:href="#about-arrow"></use>
                        </svg>
                    </div>
                </li>
                <li>
                    <div class="txt">Я отдаю эту скидку вам</div>
                    <div class="gradient-marker">3</div>
                    <div class="bott-arrow">
                        <svg class="icon-about-arrow">
                            <use xlink:href="#about-arrow"></use>
                        </svg>
                    </div>
                </li>
                <li>
                    <div class="txt">В итоге вы покупаете по оптовым<br>ценам, а поставщики не тратятся<br>на рекламу.</div>
                </li>
            </ul>
        </div>
        <div class="discount-come-from about-block">
            <div class="bottom-triangle"></div>
            <svg class="icon-discount-come-from">
                <use xlink:href="#discount-come-from"></use>
            </svg>
            <div class="block-ttl">Откуда берётся скидка?</div>
            <div class="info">Все скидки честные и вот<br>4 варианта их происхождения:</div>
            <ul class="stages">
                <li>
                    <div class="txt">Вместо затрат на рекламу</div>
                    <div class="gradient-marker">A</div>
                    <p>Поставщик тратит на рекламу и<br>продвижение 20-40% своих доходов.<br>Покупателей с ZemSkidki.ru он получает,<br>минуя эти затраты, поэтому может<br>подвинуться по цене. При этом 3% он<br>перечисляет на поддержание<br>и развитие сайта, а на оставшиеся<br>17-37% предоставляет честную скидку<br>покупателям.</p>
                </li>
                <li>
                    <div class="txt">Вместо откатов</div>
                    <div class="gradient-marker">B</div>
                    <p>Поставщик работает по рекомендации и<br>за предоставление заказа выплачивает<br>агентское вознаграждение от 10 до 30%.<br>Работая с ZemSkidki.ru, он выплачивает<br>всего 3% на поддержание и развитие<br>сайта, а на оставшиеся 7-27%<br>предоставляет честную скидку<br>покупателям.</p>
                </li>
                <li>
                    <div class="txt">Это оптовая скидка</div>
                    <div class="gradient-marker">C</div>
                    <p>Поставщик предоставляет оптовым<br>покупателям скидку 20-40%. За счёт<br>коллективных закупок ZemSkdidki.ru<br>также являются оптовым покупателем<br>и получают такую же скидку. Из них 3%<br>идёт на поддержание и развитие сайта,<br>а из оставшихся 17-37% формируется<br>честная скидка для покупателей.</p>
                </li>
                <li>
                    <div class="txt">Это дилерская скидка</div>
                    <div class="gradient-marker">D</div>
                    <p>У поставщика есть дилерская сеть <br>и своим постоянным партнёрам<br>он предоставляет скидку 20-40%. Для<br>ZemSkidki.ru он готов предоставить<br>такую-же скидку. Из них 3% идут <br>на поддержание и развитие сайта,<br>а из оставшихся 17-37% формируется<br>честная скидка для покупателей.</p>
                </li>
            </ul>
            <p class="ttl">То есть, суть моего проекта — дать вам настоящие скидки</p>
            <p class="discounts-fake">Потому что большинство скидок<br>ненастоящие, они берутся<br>из предварительных накруток.<br>Вот смотрите:</p>
        </div>
        <div class="pricing">
            <div class="standart-pricing-scheme">
                <div class="scheme-name">Стандартная схема ценообразования</div>
                <div class="scheme scheme1">
                    <div class="left">
                        <p>Цена для обычного покупателя <b>100Р</b></p>
                        <div class="percent-row">
                            <div class="item p1"><b>40%</b><br><span>Себестоимость</span></div>
                            <div class="item p2"><b>10%</b><br><span>Расходы</span></div>
                            <div class="item p3"><b>20%</b><br><span>Прибыль</span></div>
                            <div class="item p4"><b>30%</b><br><span>Реклама</span></div>
                        </div>
                    </div>
                    <div class="right"><span><b>756%</b><br>Дутые скидки</span></div>
                </div>
                <div class="pricing-text">Как видите, реальной скидкой там даже<br>не пахнет, потому что ей просто неоткуда<br>взяться! Поставщик ведь не может<br>уменьшить накладные расходы или<br>себестоимость производства, а дать скиду<br>со своей прибыли он вряд ли захочет —<br>там всего 20%, а с них ещё нужно заплатить<br>налоги...</div>
                <div class="pricing-text">Но даже если на секунду предположить,<br>что он решил подвинуться по цене и вам<br>дали настоящую скидку, она всё равно будет<br>меньше той скидки, которую дают мне<br>и другим постоянным партнёрам.</div>
                <div class="pricing-text">Это связано с тем, что для привлечения вас,<br>покупателей, поставщик тратит огромные<br>деньги на рекламу и продвижение своего<br>магазина. А когда покупателя привожу я,<br>рекламные расходы поставщика равны нулю.<br>А значит, он должен дать мне «дилерскую»<br>скидку, равную размеру этих рекламных<br>расходов. Посмотрите, как это выглядит<br>на схеме:</div>
                <div class="price-for-partners">
                    <div class="scheme-name">Цена для дилеров и партнёров</div>
                    <div class="scheme scheme2">
                        <div class="left">
                            <p>Цена для дилера <b>70Р</b></p>
                            <div class="percent-row">
                                <div class="item p1"><b>40%</b><br><span>Себестоимость</span></div>
                                <div class="item p2"><b>10%</b><br><span>Расходы</span></div>
                                <div class="item p3"><b>20%</b><br><span>Прибыль</span></div>
                            </div>
                        </div>
                        <div class="right green"><span><b>30%</b><br>Дилерская<br>скидка</span></div>
                    </div>
                </div>
                <div class="pricing-text">Как видите, дилерам дают настоящую скидку,<br>которая образуется вместо расходов<br>на рекламу. Поставщик при этом ничего<br>не теряет, а дилер зарабатывает 20-40%<br>в виде «отката».</div>
                <div class="pricing-text">В этом и есть суть моего проекта — собрать<br>надёжных поставщиков, выбить у них<br>максимальную дилерскую скидку, а затем<br>отдать её вам. Я хочу, чтобы любой из вас<br>мог придти к поставщику и сказать<br>«Я от Алексея Земскова». А затем получить<br>цены, по которым с ним работаю я.</div>
                <div class="pricing-text">Себе я оставлю всего 3% от этой скидки.<br>Если поставщик даёт скидку 10%, вам<br>достанется 7%, если даёт 20% — вы получите<br>17%, а если даёт 45% — то целых 42%.</div>
                <div class="price-for-members">
                    <div class="scheme-name">Цена для участников ZemSkidki.ru</div>
                    <div class="scheme scheme3">
                        <div class="left">
                            <p>Цена на ZemSkidki.ru<b>73Р</b></p>
                            <div class="percent-row">
                                <div class="item p1"><b>40%</b><br><span>Себестоимость</span></div>
                                <div class="item p2"><b>10%</b><br><span>Расходы</span></div>
                                <div class="item p3"><b>20%</b><br><span>Прибыль</span></div>
                                <div class="item p4"><b>3%</b><br><span>Zems</span></div>
                            </div>
                        </div>
                        <div class="right green"><span><b>27%</b><br>Реальная<br>экономия</span></div>
                    </div>
                </div>
                <div class="pricing-text-blue">В итоге вы пользуетесь скидкой, которую<br>никогда не получили бы самостоятельно,<br>а поставщик тратит на привлечение<br>клиентов всего 3% вместо обычных 20-30% </div>
                <div class="pricing-text-blue">Пользуйтесь на здоровье!<br>Всегда ваш Алексей Земсков;)</div>
                <div class="button-block icon-big view-discounts">
                    <a href="/" class="btn">
                        <svg class="icon long-ticket">
                            <use xlink:href="#long-ticket"></use>
                        </svg>
                        <div class="txt">Смотреть скидки</div>
                    </a>
                </div>
                <div class="button-block partner icon-big">
                    <a href="/cooperation/" class="btn">
                        <svg class="icon">
                            <use xlink:href="#partner"></use>
                        </svg>
                        <div class="txt">Стать партнером</div>
                    </a>
                </div>
            </div>
            <div class="rounding">
                <svg class="icon-rounding">
                    <use xlink:href="#rounding"></use>
                </svg>
                <svg class="icon-rounding right">
                    <use xlink:href="#rounding"></use>
